"use client";
import { useState, useEffect } from 'react';
import { useAppStore } from '@/lib/store';
import { Button } from '@/components/ui/button';
import { Calendar } from '@/components/ui/calendar';
import { Card, CardContent } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Dialog, DialogContent, DialogTrigger, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { toast } from 'sonner';
import { CalendarDays, History, Loader2 } from 'lucide-react';
// ‚úÖ ÂºïÂÖ•ÂêéÁ´Ø Actions
import { getCounselors, createAppointment, getAppointments, getCounselorSchedule } from '@/app/actions';

export default function BookingPage() {
  const { currentUser } = useAppStore();
  const [activeTab, setActiveTab] = useState("new");

  // Êï∞ÊçÆÁä∂ÊÄÅ
  const [counselors, setCounselors] = useState<any[]>([]);
  const [myAppointments, setMyAppointments] = useState<any[]>([]);
  
  // È¢ÑÁ∫¶Ë°®ÂçïÁä∂ÊÄÅ
  const [selectedDate, setSelectedDate] = useState<Date | undefined>(new Date());
  const [selectedTime, setSelectedTime] = useState<string>("");
  const [availableSlots, setAvailableSlots] = useState<string[]>([]); // ÂΩìÂâçÈÄâ‰∏≠ÁöÑÂí®ËØ¢Â∏àÂú®ÈÄâ‰∏≠Êó•ÊúüÁöÑÂèØÁî®Êó∂Èó¥
  const [loadingSlots, setLoadingSlots] = useState(false);
  
  // ÂΩìÂâçÊ≠£Âú®Êü•ÁúãÂì™‰∏™Âí®ËØ¢Â∏àÁöÑÊéíÁè≠
  const [targetCounselorId, setTargetCounselorId] = useState<string | null>(null);

  // 1. ÂàùÂßãÂåñÂä†ËΩΩ
  useEffect(() => {
    if (currentUser?.id) {
      getCounselors().then(setCounselors);
      getAppointments(currentUser.id, 'student').then(setMyAppointments);
    }
  }, [currentUser, activeTab]); // ÂàáÊç¢TabÊó∂Âà∑Êñ∞ÂàóË°®

  // 2. ÂΩìÁî®Êà∑ÈÄâÊã©‰∫ÜÂí®ËØ¢Â∏à ÊàñËÄÖ ÊîπÂèò‰∫ÜÊó•ÊúüÔºåÂéªÊü•ÊéíÁè≠
  useEffect(() => {
    async function fetchSlots() {
      if (!targetCounselorId || !selectedDate) return;
      
      setLoadingSlots(true);
      setSelectedTime(""); // ÈáçÁΩÆÂ∑≤ÈÄâÊó∂Èó¥

      // Ëé∑ÂèñÂë®Âá† (0ÊòØÂë®Êó•, 1ÊòØÂë®‰∏Ä... PrismaÈáåÂ≠òÁöÑÊòØ 1-5)
      let dayOfWeek = selectedDate.getDay(); 
      if (dayOfWeek === 0) dayOfWeek = 7; // ÁÆÄÂçïÂ§ÑÁêÜÂë®Êú´

      // Ë∞ÉÁî®ÂêéÁ´ØÊü•ÊéíÁè≠
      const res = await getCounselorSchedule(targetCounselorId);
      if (res.success) {
        // ËøáÊª§Âá∫Ëøô‰∏ÄÂ§©(dayOfWeek)ÂºÄÊîæÁöÑÊó∂Èó¥ÊÆµ
        const slotsForToday = res.data
          .filter((s: any) => s.dayOfWeek === dayOfWeek)
          .map((s: any) => s.timeSlot)
          .sort(); // ÊéíÂ∫è
        
        setAvailableSlots(slotsForToday);
      }
      setLoadingSlots(false);
    }

    if (activeTab === 'new') {
        fetchSlots();
    }
  }, [targetCounselorId, selectedDate]);

  // 3. Êèê‰∫§È¢ÑÁ∫¶
  const handleBook = async () => {
    if (!selectedDate || !selectedTime || !targetCounselorId || !currentUser) {
      toast.error("ËØ∑ÂÆåÂñÑÈ¢ÑÁ∫¶‰ø°ÊÅØ");
      return;
    }

    const res = await createAppointment(
      currentUser.id,
      targetCounselorId,
      selectedDate.toISOString(),
      selectedTime
    );

    if (res.success) {
      toast.success("È¢ÑÁ∫¶Áî≥ËØ∑Â∑≤Êèê‰∫§");
      setActiveTab("history"); // Ë∑≥Âà∞ÂéÜÂè≤ËÆ∞ÂΩï
      setTargetCounselorId(null); // ÂÖ≥Èó≠ÂºπÁ™ó
    } else {
      toast.error("È¢ÑÁ∫¶Â§±Ë¥•ÔºåËØ•Êó∂ÊÆµÂèØËÉΩÂ∑≤Ë¢´Âç†Áî®");
    }
  };

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center border-b pb-4">
        <h1 className="text-2xl font-bold flex items-center gap-2">
          <CalendarDays className="text-[#5D9C59]" /> Âí®ËØ¢È¢ÑÁ∫¶
        </h1>
      </div>

      <Tabs value={activeTab} onValueChange={setActiveTab} className="w-full">
        <TabsList className="mb-6">
          <TabsTrigger value="new">ÂèëËµ∑Êñ∞È¢ÑÁ∫¶</TabsTrigger>
          <TabsTrigger value="history">ÊàëÁöÑËÆ∞ÂΩï</TabsTrigger>
        </TabsList>

        <TabsContent value="new" className="space-y-6">
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
             {counselors.map((c) => (
                <div key={c.id} className="bg-white p-6 rounded-xl border shadow-sm flex flex-col gap-4">
                  <div className="flex items-center gap-4">
                    <div className="w-16 h-16 rounded-full bg-slate-200 overflow-hidden">
                      <img src={c.avatar || "https://github.com/shadcn.png"} alt="" className="w-full h-full object-cover"/>
                    </div>
                    <div>
                      <h3 className="text-lg font-bold">{c.name}</h3>
                      <p className="text-sm text-slate-500">{c.college || 'ÂøÉÁêÜ‰∏≠ÂøÉ'}</p>
                    </div>
                  </div>
                  
                  <Dialog onOpenChange={(open) => {
                      if(open) setTargetCounselorId(c.id); // ÊâìÂºÄÂºπÁ™óÊó∂ËÆæÁΩÆÁõÆÊ†áÂí®ËØ¢Â∏à
                  }}>
                    <DialogTrigger asChild>
                        <Button className="w-full bg-[#5D9C59] hover:bg-[#3E6D3B]">Á´ãÂç≥È¢ÑÁ∫¶</Button>
                    </DialogTrigger>
                    <DialogContent className="max-w-md">
                      <DialogHeader><DialogTitle>È¢ÑÁ∫¶ {c.name} ËÄÅÂ∏à</DialogTitle></DialogHeader>
                      
                      <div className="space-y-6 py-4">
                        {/* 1. ÈÄâÊó•Êúü */}
                        <div className="space-y-2">
                            <label className="text-sm font-medium">1. ÈÄâÊã©Êó•Êúü</label>
                            <div className="border rounded-md p-2 flex justify-center">
                                <Calendar 
                                    mode="single" 
                                    selected={selectedDate} 
                                    onSelect={setSelectedDate} 
                                    disabled={(date) => {
                                        // Á¶ÅÁî®ËøáÂéªÁöÑÊó∂Èó¥ Âíå Âë®Êú´(ÂÅáËÆæÂë®Êú´‰∏çÂºÄÊîæ)
                                        const day = date.getDay();
                                        return date < new Date(new Date().setHours(0,0,0,0)) || day === 0 || day === 6;
                                    }}
                                />
                            </div>
                        </div>

                        {/* 2. ÈÄâÊó∂Èó¥ (Âä®ÊÄÅ‰ªéÊï∞ÊçÆÂ∫ìÂä†ËΩΩ) */}
                        <div className="space-y-2">
                            <label className="text-sm font-medium">2. ÈÄâÊã©Êó∂Èó¥ÊÆµ</label>
                            {loadingSlots ? (
                                <div className="text-sm text-slate-500 flex items-center gap-2"><Loader2 className="animate-spin w-4 h-4"/> Ê≠£Âú®Êü•ËØ¢ÊéíÁè≠...</div>
                            ) : availableSlots.length > 0 ? (
                                <Select value={selectedTime} onValueChange={setSelectedTime}>
                                    <SelectTrigger>
                                        <SelectValue placeholder="ËØ∑ÈÄâÊã©ÂèØÁî®Êó∂ÊÆµ" />
                                    </SelectTrigger>
                                    <SelectContent>
                                        {availableSlots.map(slot => (
                                            <SelectItem key={slot} value={slot}>{slot}</SelectItem>
                                        ))}
                                    </SelectContent>
                                </Select>
                            ) : (
                                <div className="text-sm text-red-500 bg-red-50 p-2 rounded">
                                    ËØ•Âí®ËØ¢Â∏àÂú® {selectedDate?.toLocaleDateString()} ÊöÇÊó†ÊéíÁè≠ÔºåËØ∑ÂàáÊç¢Êó•Êúü„ÄÇ
                                </div>
                            )}
                        </div>
                      </div>

                      <Button onClick={handleBook} disabled={!selectedTime} className="w-full bg-[#5D9C59]">
                          Á°ÆËÆ§Êèê‰∫§Áî≥ËØ∑
                      </Button>
                    </DialogContent>
                  </Dialog>
                </div>
              ))}
          </div>
        </TabsContent>

        <TabsContent value="history">
          <div className="space-y-4">
            {myAppointments.map((apt) => (
              <div key={apt.id} className="p-4 bg-white border rounded-lg flex justify-between items-center">
                <div className="flex items-center gap-4">
                  <History className="text-slate-400" />
                  <div>
                    <div className="font-bold">{apt.counselor.name} ËÄÅÂ∏à</div>
                    <div className="text-sm text-slate-500">
                      {new Date(apt.date).toLocaleDateString()} {apt.time}
                    </div>
                  </div>
                </div>
                <Badge variant={apt.status === 'confirmed' ? 'default' : 'secondary'}
                   className={apt.status === 'pending' ? 'bg-blue-100 text-blue-700 hover:bg-blue-200 border-0' : ''}
                >
                  {apt.status === 'confirmed' ? 'È¢ÑÁ∫¶ÊàêÂäü' : apt.status === 'rejected' ? 'Â∑≤ÊãíÁªù' : 'Â∑≤Êèê‰∫§Áî≥ËØ∑'}
                </Badge>
              </div>
            ))}
            {myAppointments.length === 0 && <div className="text-center py-10 text-slate-400">ÊöÇÊó†ËÆ∞ÂΩï</div>}
          </div>
        </TabsContent>
      </Tabs>
    </div>
  );
}