export async function getAdminDashboardStats() {
  //noStore(); // üà≤ Á¶ÅÁî®ÁºìÂ≠òÔºåÁ°Æ‰øùÊï∞ÊçÆÂÆûÊó∂

  try {
    const [studentCount, appointmentCount, completedCount, rawChartData] = await Promise.all([
      // 1. Ê≥®ÂÜåÂ≠¶ÁîüÊÄªÊï∞
      prisma.user.count({ 
        where: { role: 'student' } 
      }),

      // 2. ÊúâÊïàÈ¢ÑÁ∫¶ÊÄªÈáè (ÂÖ≥ÈîÆ‰øÆÊîπÔºÅÔºÅÔºÅ)
      // ‰ª•ÂâçÁªüËÆ°‰∫Ü pendingÔºåÁé∞Âú®Êàë‰ª¨Âè™ÁªüËÆ° 'confirmed' Âíå 'completed'
      // ËøôÊ†∑ÔºöÂ≠¶ÁîüÂàöÁî≥ËØ∑Êó∂(pending)‰∏çÂç†Êï∞ÔºåËÄÅÂ∏àÁÇπ‰∫ÜÂêåÊÑè(confirmed)Êï∞Â≠óÊâç‰ºö +1
      prisma.appointment.count({
        where: {
          status: {
            in: ['confirmed', 'completed'] // ‚úÖ Âè™ÁÆó‚ÄúÂ∑≤Á°ÆËÆ§‚ÄùÂíå‚ÄúÂ∑≤ÂÆåÊàê‚ÄùÁöÑ
          }
        }
      }),

      // 3. Â∑≤ÂÆåÊàêÈ¢ÑÁ∫¶
      prisma.appointment.count({ 
        where: { status: 'completed' } 
      }),

      // 4. ÂõæË°®Êï∞ÊçÆÊ∫ê (ÂêåÊ†∑Âè™ÁªüËÆ°ÊúâÊïàÁöÑ)
      prisma.appointment.findMany({
        where: {
          date: {
            gte: new Date(new Date().setDate(new Date().getDate() - 6))
          },
          status: {
            in: ['confirmed', 'completed'] // ‚úÖ ÂõæË°®‰πüÂè™ÊòæÁ§∫ËÄÅÂ∏àÊé•ÂçïÂêéÁöÑ
          }
        },
        select: { date: true }
      })
    ]);

    // Âç±Êú∫Âπ≤È¢ÑÁªüËÆ° (È¢ÑÁïô)
    let crisisCount = 0;

    // ËÆ°ÁÆóÁªìÊ°àÁéá
    const completionRate = appointmentCount > 0 
      ? Math.round((completedCount / appointmentCount) * 100) + "%" 
      : "0%";

    // ÁîüÊàêÂõæË°®Êï∞ÊçÆ
    const chartData = [];
    const weekMap = ['Âë®Êó•', 'Âë®‰∏Ä', 'Âë®‰∫å', 'Âë®‰∏â', 'Âë®Âõõ', 'Âë®‰∫î', 'Âë®ÂÖ≠'];

    for (let i = 6; i >= 0; i--) {
      const d = new Date();
      d.setDate(d.getDate() - i);
      const month = d.getMonth() + 1;
      const day = d.getDate();
      const dateKey = `${month}/${day}`;
      const weekLabel = weekMap[d.getDay()];

      const count = rawChartData.filter(apt => {
        const aptDate = new Date(apt.date);
        return aptDate.getDate() === day && aptDate.getMonth() + 1 === month;
      }).length;

      chartData.push({ 
        name: dateKey, 
        day: weekLabel, 
        visits: count 
      });
    }

    return {
      success: true,
      data: {
        stats: {
          students: studentCount,
          appointments: appointmentCount, // ËøôÈáåÁé∞Âú®ÊòæÁ§∫ÁöÑÊòØ‚ÄúÁîüÊïàÈ¢ÑÁ∫¶Êï∞‚Äù
          crisis: crisisCount,
          rate: completionRate
        },
        chart: chartData
      }
    };

  } catch (error) {
    console.error("Ëé∑ÂèñÁúãÊùøÊï∞ÊçÆÂ§±Ë¥•:", error);
    return { success: false, error: "Âä†ËΩΩÂ§±Ë¥•" };
  }
}

