// Êõ¥Êñ∞Á≥ªÁªüÈÖçÁΩÆ
export async function updateSystemSettings(data: any) {
  try {
    // ÂÖàÊâæÂà∞Á¨¨‰∏ÄÊù°ËÆ∞ÂΩïÁöÑ ID
    const first = await prisma.systemConfig.findFirst();
    
    if (first) {
      await prisma.systemConfig.update({
        where: { id: first.id },
        data: {
          platformName: data.platformName,
          hotline: data.hotline,
          openHours: data.openHours,
          maintenanceMode: data.maintenanceMode === 'true' || data.maintenanceMode === true
        }
      });
      return { success: true };
    }
    return { success: false, error: "Êú™ÊâæÂà∞ÈÖçÁΩÆËÆ∞ÂΩï" };
  } catch (error) {
    console.error(error);
    return { success: false, error: "‰øùÂ≠òÂ§±Ë¥•" };
  }
}

