// 2. ÊâπÈáèÊõ¥Êñ∞ÊéíÁè≠ (Âí®ËØ¢Â∏àÁ´Ø‰ΩøÁî®)
// slots Ê†ºÂºèÔºö[{ dayOfWeek: 1, timeSlot: "08:30 - 09:20", isAvailable: true }, ...]
export async function updateSchedule(counselorId: string, slots: any[]) {
  try {
    // ‰ΩøÁî®‰∫ãÂä°ÔºåÂÖàÂà†ÂêéÂä†ÔºåÊàñËÄÖÈÄê‰∏™ upsert
    // ÁÆÄÂçïËµ∑ËßÅÔºåÊàë‰ª¨‰ΩøÁî® transaction ÈÄê‰∏™ upsert
    const ops = slots.map(slot => 
      prisma.schedule.upsert({
        where: {
          counselorId_dayOfWeek_timeSlot: {
            counselorId,
            dayOfWeek: slot.dayOfWeek,
            timeSlot: slot.timeSlot
          }
        },
        update: { isAvailable: slot.isAvailable },
        create: {
          counselorId,
          dayOfWeek: slot.dayOfWeek,
          timeSlot: slot.timeSlot,
          isAvailable: slot.isAvailable
        }
      })
    );
    
    await prisma.$transaction(ops);
    return { success: true };
  } catch (error) {
    console.error(error);
    return { success: false, error: "‰øùÂ≠òÂ§±Ë¥•" };
  }
}

